
























var userLoginInfo;
export default {
	data() {
		return {
			cardList: []
		}
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		// console.log(userLoginInfo.id);
		this.getCard();
	},
	// 		onLoad() {
	// 			this.getCard();
	// 		},
	methods: {
		// 添加银行卡
		// url="./card_holding"
		addCard: function() {
			uni.redirectTo({
				url: './card_holding'
			});
		},
		// 获取银行卡详情
		cardDetail: function(cardId) {
			uni.redirectTo({
				url: './addCard_detail?id=' + cardId
			});
		},
		getCard: function() {
			uni.request({
				url: this.apiUrl + 'user/getBankList',
				method: 'GET',
				data: {
					uid: userLoginInfo.id
				},
				success: res => {
					if (res.data.code === 0) {
						this.cardList = res.data.data;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}

}
